@import "src/sass/vars";
@import "src/sass/mixins";

.image {
  min-height: 350px;
  background-color: #d6d6d6;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  background-position: center;
  &:before {
    content: "";
    position: absolute;
    background-color: $bgcolor;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.4;
  }
  @media (max-width: $mq2+px) {
    min-height: 220px;
  }
  @media (max-width: $mq3+px) {
    min-height: 350px;
  }

}

.viewBtn {
  color: $maincolor;
  border: 1px solid $maincolor;
  background-color: $bgcolor;
  font-size: 16px;
  letter-spacing: 4px;
  position: relative;
  width: 8rem;
  line-height: 35px;
  text-align: center;
  z-index: 5;
  opacity: 0.7;

  &:after {
    content: "";
    width: 4px;
    height: 4px;
    right: 0;
    bottom: 0;
    background: $maincolor;
    position: absolute;
    transition: 0.3s ease-out all;
    z-index: 5;
  }

  &:before {
    position: absolute;
    content: "";
    width: 0;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
    background-color: $maincolor;
    transition: all 0.35s ease;
  }

  &:hover {
    color: $bgcolor;
  }

  &:hover:before {
    left: 0;
    width: 100%;
  }

  &:active {
    top: 2px;
  }
}
.subtitle_wrapper {
  @include flexCenter;
  padding: 10px 0;
  flex-direction: column;
  font-size: 1.2rem;
  //background-color: rgba(29, 29, 29, 0.3);
  @media (max-width: $mq3+px) {
    font-size: 1.6rem;
  }@media (max-width: $mq4+px) {
    font-size: 2rem;
  }
}
.description {
  text-align: center;
}
